import React from "react";

import Table from "react-bootstrap/Table";

import { connect } from "react-redux";
import { updateStopForm } from "../../actions/stopFormActions";

function TableRow(props) {
    return (
        <tr>
            <td>{props.index}</td>
            <td>{props.stop.stopAddress}</td>
            <td>{props.stop.stopName}</td>
            <td>{props.stop.latitude}</td>
            <td>{props.stop.longitude}</td>
            <td>
                <button onClick={
                    (_) => {
                        
                        props.updateStopForm({
                            stopName: props.stop.stopName,
                            longitude: props.stop.longitude,
                            latitude: props.stop.latitude
                        })
                        props.updateLastId(props.stop.stopId)
                        console.log(props.stop.stopId)
                    }
                }>
                    Edit
                </button>
            </td>
        </tr>
    )
}

class StopTable extends React.Component {
    render() {
        return (
            <div className="stop-table">
                <h2>All stops</h2>
                <Table striped bordered hover>
                    <thead>
                    <tr>
                        <th>#</th>
                        <th>Stop Address</th>
                        <th>Stop Name</th>
                        <th>Lat</th>
                        <th>Long</th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        (this.props.stops !== 'undefined') ?
                            this.props.stops.map((stop, index) => {
                                return (
                                    <TableRow key={index} stop={stop} index={index} updateLastId={this.props.updateLastId} updateStopForm={this.props.updateStopForm}/>
                                );
                            })
                            :
                            (
                                <tr>
                                    <td>Loading...</td>
                                </tr>
                            )            
                    }
                    </tbody>
                </Table>
            </div>
        );
    }
}


export default connect(null, { updateStopForm })(StopTable);