import React from "react";
import "./App.scss";

import Sidebar from "./components/Sidebar/Sidebar.js";
import AddStops from "./components/Stops/AddStops";

import AddDrivers from "./components/BusDriver/AddDrivers";
import ViewAllDrivers from "./components/BusDriver/ViewAllDrivers";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  return (
    <Router>
      <Sidebar />
      <Switch>
        <Route exact path="/">
          <div className="App">
            <div className="content-main">Home</div>
          </div>
        </Route>

        <Route exact path="/stops/add">
          <div className="App">
            <div className="content-main">
              <AddStops />
            </div>
          </div>
        </Route>
        <Route exact path="/drivers/add">
          <div className="App">
            <div className="content-main">
              <AddDrivers />
            </div>
          </div>
        </Route>
        <Route exact path="/drivers/all">
          <div className="App">
            <div className="content-main">
              <ViewAllDrivers />
            </div>
          </div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
