import React, { Component } from "react";

import Form from "react-bootstrap/Form";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";

import StopTable from "./StopTable";

import { EndPoints } from "../../config";
import { connect } from "react-redux";

import { addBusStop, getAllBusStops } from "../../actions/stopFormActions";

class AddStopsForms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stopAddress: "",
      lastId: 0,
    };
  }

  componentDidMount() {
    this.props
      .getAllBusStops()
      .then((_) => {
        return Math.max(0, this.props.stops.map((stop) => stop.stopId).pop());
      })
      .then((lastId) => {
        this.setState({
          lastId,
        });
      });
  }

  updateLastId = (id) => {
    this.setState({
      lastId: id,
    });
  };

  componentDidUpdate() {}

  onAddressChangeHandler = (event) => {
    const stopAddress = event.currentTarget.value;

    this.setState({
      stopAddress,
    });
  };

  submitHandler = (event) => {
    const { longitude, latitude, stopName } = this.props;
    const { stopAddress } = this.state;

    const stopId = Math.max(
      0,
      this.props.stops.map((stop) => stop.stopId).pop()
    );

    this.props.addBusStop({
      stopId,
      longitude,
      latitude,
      stopName,
      stopAddress,
    });

    this.setState({
      stopAddress: "",
    });

    event.preventDefault();
  };

  render() {
    return (
      <Container fluid>
        <Row>
          <Col lg={12} md={12}>
            <h2>Add Stop</h2>
          </Col>
        </Row>
        <Row>
          <Col lg={12} md={12}>
            <Form onSubmit={this.submitHandler} action={EndPoints.addRoutes}>
              <Row className="align-items-end">
                <Col md={3}>
                  <Form.Group controlId="stopAddress">
                    <Form.Label>Stop Address</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Stop Address"
                      onChange={this.onAddressChangeHandler}
                      value={this.state.stopAddress}
                      required="required"
                    />
                  </Form.Group>
                </Col>
                <Col md={3}>
                  <Form.Group controlId="stopName">
                    <Form.Label>Stop Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Stop Name"
                      value={this.props.stopName}
                    />
                  </Form.Group>
                </Col>
                <Col md={3}>
                  <Form.Group controlId="latitute">
                    <Form.Label>Latitute</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Latitute"
                      value={this.props.latitude}
                    />
                  </Form.Group>
                </Col>
                <Col md={3}>
                  <Form.Group controlId="latitute">
                    <Form.Label>Longitude</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Longitude"
                      value={this.props.longitude}
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col md={3}>
                  <Form.Group>
                    <Button type="submit">Add Route</Button>
                  </Form.Group>
                </Col>
              </Row>
            </Form>
          </Col>
        </Row>
        <Row>
          <Col md={12}>
            <StopTable
              updateLastId={this.updateLastId}
              stops={this.props.stops}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

const mapStateToProps = (state) => ({
  stopAddress: state.stopForm.stopAddress,
  stopName: state.stopForm.stopName,
  longitude: state.stopForm.longitude,
  latitude: state.stopForm.latitude,
  stops: state.stopForm.stops,
});

export default connect(mapStateToProps, {
  addBusStop,
  getAllBusStops,
})(AddStopsForms);
