import React from "react";

import { Formik } from "formik";
import * as Yup from "yup";

import Form from "react-bootstrap/Form";
import Col from "react-bootstrap/Col";
import InputGroup from "react-bootstrap/InputGroup";
import Button from "react-bootstrap/Button";

const schema = Yup.object({
  driverName: Yup.string().required(),
  lastName: Yup.string().required(),
  driverPhone: Yup.number().required(),
  cnic: Yup.string().required(),
  cnicExpiry: Yup.string().required(),
  file: Yup.mixed().required(),
});

class Thumb extends React.Component {
  state = {
    loading: false,
    thumb: undefined,
  };

  componentWillReceiveProps(nextProps) {
    if (!nextProps.file) {
      return;
    }

    this.setState({ loading: true }, () => {
      let reader = new FileReader();

      reader.onloadend = () => {
        this.setState({ loading: false, thumb: reader.result });
      };

      reader.readAsDataURL(nextProps.file);
    });
  }

  render() {
    const { file } = this.props;
    const { loading, thumb } = this.state;

    if (!file) {
      return null;
    }

    if (loading) {
      return <p>loading...</p>;
    }

    return (
      <img
        src={thumb}
        alt={file.name}
        className="img-thumbnail mt-2"
        height={200}
        width={200}
      />
    );
  }
}

class AddDriverForm extends React.Component {
  render() {
    return (
      <Formik
        validationSchema={schema}
        onSubmit={(values, actions) => {
          setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            actions.setSubmitting(false);
          }, 1000);
        }}
        initialValues={{
          driverName: "",
          lastName: "",
          driverPhone: "",
          cnic: "",
          cnicExpiry: "",
          driverImage: null,
          licenseNo: "",
          emergencyContact: "",
          licenseExpiry: "",
        }}
      >
        {({
          handleSubmit,
          handleChange,
          handleBlur,
          values,
          touched,
          isValid,
          setFieldValue,
          errors,
        }) => (
          <Form onSubmit={handleSubmit}>
            <Form.Control
              type="hidden"
              placeholder="driverRating"
              name="driverRating"
              value={values.driverRating}
            />
            <Form.Row>
              <Form.Group as={Col} md="6" controlId="validationFormik01">
                <Form.Label>Driver name</Form.Label>
                <Form.Control
                  type="text"
                  name="driverName"
                  value={values.driverName}
                  onChange={handleChange}
                  isInvalid={!!errors.driverName}
                />
                <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} md="6" controlId="validationFormik02">
                <Form.Label>Driver Phone</Form.Label>
                <Form.Control
                  type="text"
                  name="driverPhone"
                  placeholder="03001234567"
                  value={values.driverPhone}
                  onChange={handleChange}
                  isInvalid={!!errors.driverPhone}
                />
                <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group as={Col} md="6" controlId="cnic">
                <Form.Label>CNIC</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="41010-1234567-1"
                  aria-describedby="inputGroupPrepend"
                  name="cnic"
                  value={values.cnic}
                  onChange={handleChange}
                  isInvalid={!!errors.cnic}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.cnic}
                </Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} md="6" controlId="validationFormicnicExpiry">
                <Form.Label>CNIC Expiry Date</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="06/2022"
                  name="cnicExpiry"
                  value={values.cnicExpiry}
                  onChange={handleChange}
                  isInvalid={!!errors.cnicExpiry}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.cnicExpiry}
                </Form.Control.Feedback>
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group as={Col} md="6" controlId="validationFormik02">
                <Form.Label>Emergency Contact</Form.Label>
                <Form.Control
                  type="text"
                  name="emergencyContact"
                  placeholder="03001234567"
                  value={values.emergencyContact}
                  onChange={handleChange}
                  isInvalid={!!errors.emergencyContact}
                />
                <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} md="6" controlId="validationFormik05">
                <Form.Label>Blood Group</Form.Label>
                <Form.Control
                  as="select"
                  name="BloodGroup"
                  value={values.BloodGroup}
                  onChange={handleChange}
                  isInvalid={!!errors.BloodGroup}
                  custom
                >
                  <option>AB+</option>
                  <option>A+</option>
                  <option>A-</option>
                  <option>B+</option>
                  <option>B-</option>
                  <option>AB+</option>
                  <option>AB-</option>
                  <option>O+</option>
                  <option>O-</option>
                </Form.Control>
                <Form.Control.Feedback type="invalid">
                  {errors.BloodGroup}
                </Form.Control.Feedback>
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group as={Col} md="6" controlId="validationFormik06">
                <Form.Label>License No.</Form.Label>
                <Form.Control
                  type="text"
                  name="licenseNo"
                  placeholder="123456789"
                  value={values.licenseNo}
                  onChange={handleChange}
                  isInvalid={!!errors.licenseNo}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.licenseNo}
                </Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} md="6" controlId="validationFormik06">
                <Form.Label>License Expiry</Form.Label>
                <Form.Control
                  type="text"
                  name="licenseExpiry"
                  placeholder="06/2025"
                  value={values.licenseExpiry}
                  onChange={handleChange}
                  isInvalid={!!errors.licenseExpiry}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.licenseExpiry}
                </Form.Control.Feedback>
              </Form.Group>
            </Form.Row>
            <Form.Row>
              <Form.Group as={Col} md="12" controlId="validationFormik06">
                <Form.Label>Driver Image</Form.Label>
                <Form.Control
                  type="file"
                  name="driverImage"
                  onChange={(event) => {
                    setFieldValue("driverImage", event.currentTarget.files[0]);
                  }}
                  isInvalid={!!errors.driverImage}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.driverImage}
                </Form.Control.Feedback>
                <Thumb file={values.file} />
              </Form.Group>
            </Form.Row>
            <Button type="submit">Submit form</Button>
          </Form>
        )}
      </Formik>
    );
  }
}

export default AddDriverForm;
