import React from "react";

import { Link } from "react-router-dom";

import "./Sidebar.scss";

class Sidebar extends React.Component {
  render() {
    return (
      <div className="Sidebar">
        <nav>
          <ul>
            <li>
              <Link to="/routes">Routes</Link>
            </li>
            <li>
              Stops
              <ul>
                <li>
                  <Link to="/stops/add">Add Stops</Link>
                </li>
                <li>
                  <Link to="/stops/all">View All</Link>
                </li>
              </ul>
            </li>
            <li>
              Drivers
              <ul>
                <li>
                  <Link to="/drivers/add">Add Drivers</Link>
                </li>
                <li>
                  <Link to="/drivers/all">View All</Link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    );
  }
}

export default Sidebar;
