import React, { Component } from "react";
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";
import { GOOGLE_MAP_API_KEY } from "../../config";

import { connect } from "react-redux";
import { updateStopForm } from "../../actions/stopFormActions";

const mapStyles = {
  width: "100%",
  height: "457px",
  maxWidth: "100%",
  position: "relative",
  marginBottom: "60px",
};

export class GoogleMap extends Component {
  state = {
    showingInfoWindow: false, //Hides or the shows the infoWindow
    activeMarker: {}, //Shows the active marker upon click
    selectedPlace: {}, //Shows the infoWindow to the selected place upon a marker
    currentMarker: {
      stopName: "",
      longitude: 0,
      latitude: 0,
    },
    markers: [
      {
        name: "Karachi",
        position: {
          lat: 24.8607,
          lng: 67.0011,
        },
      },
    ],
  };

  componentDidMount() {
    // console.log(new google.maps.Geocoder);
  }

  // onMarkerClick = (props, marker, e) =>
  //   this.setState({
  //     selectedPlace: props,
  //     activeMarker: marker,
  //     showingInfoWindow: true
  //   });

  onMarkerDragEnd = (coord, index) => {
    const _this = this;
    const { latLng } = coord;
    const lat = latLng.lat();
    const lng = latLng.lng();
    console.log(
      {
        lat,
        lng,
      },
      coord
    );

    let geocoder = new this.props.google.maps.Geocoder();
    let stopName = "";

    geocoder.geocode({ location: { lat, lng } }, function (results, status) {
      if (status === "OK") {
        if (results[0]) {
          stopName = results[0].formatted_address;
          // console.log(results[0].formatted_address);
          _this.setState({
            currentMarker: { stopName, lat, lng },
          });
          _this.props.updateStopForm({
            stopName: stopName,
            longitude: lng,
            latitude: lat,
          });
        } else {
          window.alert("No results found");
        }
      } else {
        window.alert("Geocoder failed due to: " + status);
      }
    });
  };

  render() {
    return (
      <div id="google-map">
        <h1>Google Map</h1>
        <Map
          google={this.props.google}
          zoom={14}
          style={mapStyles}
          initialCenter={{
            lat: 24.8607,
            lng: 67.0011,
          }}
        >
          {this.state.markers.map((marker, index) => (
            <Marker
              draggable={true}
              onDragend={(t, map, coord) => this.onMarkerDragEnd(coord, index)}
              name={"Kenyatta International Convention Centre"}
            />
          ))}
        </Map>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  stopName: state.stopName,
  longitude: state.longitude,
  latitude: state.latitude,
});

export default connect(mapStateToProps, { updateStopForm })(
  GoogleApiWrapper({
    apiKey: GOOGLE_MAP_API_KEY,
  })(GoogleMap)
);
