import { UPDATE_ADD_STOP_FORM, ADD_BUS_STOP, GET_ALL_STOPS } from "./types";
import { EndPoints } from "../config";
import axios from "axios";

export function updateStopForm(payload) {
  return function (dispatch) {
    dispatch({
      type: UPDATE_ADD_STOP_FORM,
      payload: payload,
    });
  };
}

export function addBusStop(payload) {
  return function (dispatch, getState) {
    const s = getState();
    console.log(s);
    axios({
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
      },
      url: EndPoints.addStops,
      method: "POST",
      data: JSON.stringify(payload),
    })
      .then((response) => {
        console.log(response);
        dispatch({
          type: ADD_BUS_STOP,
          payload: payload,
        });
      })
      .catch((err) => console.log(err));
  };
}

export function getAllBusStops() {
  return function (dispatch) {
    return axios
      .get(EndPoints.addStops)
      .then((payload) => {
        const stopsData = payload.data.map((stop) => {
          return stop.properties;
        });
        return stopsData;
      })
      .then((stopsData) => {
        console.log("stopsData", stopsData);
        dispatch({
          type: GET_ALL_STOPS,
          payload: stopsData,
        });
      });
  };
}
