import {
  UPDATE_ADD_STOP_FORM,
  GET_ALL_STOPS,
  ADD_BUS_STOP,
} from "../actions/types";

const initialState = {
  stopId: 0,
  stopName: "",
  stopAddress: "",
  longitude: 0,
  latitude: 0,
  stops: [],
};

export default function (state = initialState, action) {
  switch (action.type) {
    case UPDATE_ADD_STOP_FORM:
      return {
        ...state,
        stopAddress: action.payload.stopAddress,
        stopName: action.payload.stopName,
        longitude: action.payload.longitude,
        latitude: action.payload.latitude,
      };
    case ADD_BUS_STOP:
      console.log("Last ID", state);
      return {
        ...state,
        stopAddress: "",
        stopName: "",
        longitude: 0,
        latitude: 0,
        stops: [...state.stops, action.payload.stops], //
      };
    case GET_ALL_STOPS:
      return {
        ...state,
        stops: action.payload,
      };
    default:
      return state;
  }
}
